.accordion{


}

.accordion-header {
    cursor: pointer;
    background-color: hsla(258, 97%, 49%, 0.102);
    padding-right:  1rem; 
    padding-left:  1rem;
    padding-top:  .2rem;
    padding-bottom:  .2rem;
    font-weight: bolder;
    color: #4d04f5;
    font-family: sans-serif;
    margin: 15px;
    box-shadow: 0 4px 8px 0 #4d04f53b;
    font-size: 70%;
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .accordion-body {
    padding: 3rem;
    background-color: #d3d3d30a;
    box-shadow: 0 4px 8px 0 #4d04f53b;
    padding-top: 5px;
    margin: 15px;
  }

  .accordion-body-box{
    margin-bottom: 5%;
    letter-spacing: .025em;
    font-weight: 600;
    font-size: 1rem;
    line-height: 1rem;
    margin-top: 25px;
    margin-bottom: 0.5rem;
    z-index: 10;
    font-family: sans-serif;
  }

  .accordion-ul{
    padding-left: 15px; 
    margin-bottom: 15px;
    color: rgb(17, 15, 70); 
    list-style-type: square;
  }

  @media screen and (max-width: 600px) {
    .accordion-header {
        font-size: 50%;
        padding-right:  .5rem; 
        padding-left:  .5rem;
    }
  }

  @media screen and (max-width: 500px) {
    .accordion-header {
        font-size: 8px;
        margin-top: 15px;
        margin-bottom: 15px;
        margin-left: 5px;
        margin-right: 5px;
    }

    .accordion-body {
        padding: .8rem;
        margin: 5px;
    }

    .accordion-indicator {
        font-size: 80%;
    }

    .work-tech{
        font-size: .4rem;
    }

    .accordion-body-box{
        font-size: .75rem;
        margin-top: 0px;
        margin-bottom: 0px;
    }

    .accordion-ul{
        font-size: 14px;
        margin-top: 0px;
    }

    
  }
  
  
  .accordion-header:hover {
    background-color: #eee;
  }
  
  .accordion-image {
    width: 70px;
    position: absolute;
    left: 30px;
  }
  
  .accordion-indicator {
    float: right;
    font-size: 120%;
    margin-left: auto;
  }


  .work-tech{
    --tw-text-opacity: 1;
    line-height: 1.25rem;
    font-weight: 500;
    font-size: .75rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    background-color: #4d04f51a;
    border-radius: 9999px;
    align-items: center;
    display: flex;
    box-sizing: border-box;
    border: 0 solid #e5e7eb;
    color: #4d04f5;
    margin-bottom: 5px;
  }

  .date{
    color: rgb(77, 4, 245);
    float: right;
    text-transform: uppercase;
  }
 


 

